
window.onload = function (ev) {
  $('.pictures__preview').magnificPopup({
    type: 'image',
    gallery:{
      enabled:true
    },
    removalDelay: 100
  });

  var navBtn = $('.nav__part');

  $(navBtn).on('click', function (e) {
    var dataScroll = e.currentTarget.getAttribute('data-scroll');
    if ( dataScroll ) {
      var scrollTo = document.querySelector(dataScroll);
      if ( scrollTo ) {
        e.preventDefault();
        $(window).scrollTo( {top: scrollTo.offsetTop - 70 + 'px', left:'0px'} ,  500)
      };
    };
  });



  $(".hamburger-button").click(function() {
    $(this).toggleClass("active");
    $(".nav").toggleClass("active");
  });


  var $header = $('.header'),
      $upBtn = $('.up');
  $upBtn.on('click', function () {
    $(window).scrollTo( {top:'0px', left:'0px'} ,  500)
  });

  if ( window.pageYOffset > 0 ) {
    $header.addClass('scroll');
  }

  window.addEventListener('scroll', function(e) {
    if ( this.pageYOffset > 0 ) {
      $header.addClass('scroll');
    } else  {
      $header.removeClass('scroll');
    }

    if( this.pageYOffset > 100 ) {
      $upBtn.addClass('active');
    } else {
      $upBtn.removeClass('active');
    }
  });
  var $preloader = $('.preloader');
  $preloader.detach();
};

